import React, { useContext, useEffect, useState } from "react";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import CreateIcon from "@mui/icons-material/Create";
import DeleteIcon from "@mui/icons-material/Delete";
import { NavLink } from "react-router-dom";
import { adddata, deletdata, updatedata } from "./context/Contextprovide";
const Home = () => {
  const [getuserdata, SetUserdata] = useState([]);
  // console.log(getuserdata);

  const { udata, setudata } = useContext(adddata);
  const { updata, setupdata } = useContext(updatedata);
  const { dltdata, setdltdata } = useContext(deletdata);
  const getdata = async (e) => {
    const res = await fetch("http://localhost:4000/getdata", {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });
    const data = await res.json();
    console.log(data);

    if (res.status === 404 || !data) {
      alert("Error");
      // console.log("error");
    } else {
      SetUserdata(data);
      // console.log("Get data");
    }
  };

  useEffect(() => {
    getdata();
  }, []);

  const deleteuser = async (id) => {
    const res2 = await fetch(`http://localhost:4000/deleteuser/${id}`, {
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
    });

    const deletedata = await res2.json();
    console.log(deletedata);

    if (res2.status === 404 || !deletedata) {
      alert("Error");
      console.log("error");
    } else {
      // setInp(deletedata);
      //console.log("user deleted");
      setdltdata(deletdata);
      getdata();
    }
  };
  return (
    <>
      {udata ? (
        <>
          <div
            class="alert alert-success alert-dismissible fade show"
            role="alert"
          >
            <strong>Success !</strong> Data Added Successfully .
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
        </>
      ) : (
        ""
      )}

      {updata ? (
        <>
          <div
            class="alert alert-primary alert-dismissible fade show"
            role="alert"
          >
            <strong>Success !</strong> Data Updated Successfully .
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
        </>
      ) : (
        ""
      )}

      {dltdata ? (
        <>
          <div
            class="alert alert-danger alert-dismissible fade show"
            role="alert"
          >
            <strong>Success !</strong> Data Deleted Successfully .
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="alert"
              aria-label="Close"
            ></button>
          </div>
        </>
      ) : (
        ""
      )}
      <div className="mt-5">
        <div className="container">
          <div className="add_btn mt-2 mb-2">
            <NavLink to="/register" className="btn btn-primary">
              Add data
            </NavLink>
          </div>

          <table class="table">
            <thead>
              <tr className="table-dark">
                <th scope="col">Id</th>
                <th scope="col">Username</th>
                <th scope="col">email</th>
                <th scope="col">job</th>
                <th scope="col">Salary</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              {getuserdata.map((element, id) => {
                return (
                  <>
                    <tr>
                      <th scope="row">{id + 1}</th>
                      <td>{element.name}</td>
                      <td>{element.email}</td>
                      <td>{element.job}</td>
                      <td>{element.salary}</td>
                      <td className="d-flex justify-content-between">
                        <NavLink to={`view/${element._id}`}>
                          <button className="btn btn-success">
                            <RemoveRedEyeIcon />
                          </button>
                        </NavLink>
                        <NavLink to={`edit/${element._id}`}>
                          <button className="btn btn-primary">
                            <CreateIcon />
                          </button>
                        </NavLink>
                        <button
                          onClick={() => deleteuser(element._id)}
                          className="btn btn-danger"
                        >
                          <DeleteIcon />
                        </button>
                      </td>
                    </tr>
                  </>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
};

export default Home;
