import React, { useContext, useEffect, useState } from "react";
import { NavLink, useNavigate, useParams } from "react-router-dom";
import { updatedata } from "./context/Contextprovide";

const Edit = () => {

  const {updata,setUpdata}=useContext(updatedata);

  const history = useNavigate("");
  const [inpvalue, setInp] = useState({
    name: "",
    email: "",
    job: "",
    salary: "",
  });

  const setData = (e) => {
    console.log(e.target.value);
    const { name, value } = e.target;
    setInp((preval) => {
      return {
        ...preval,
        [name]: value,
      };
    });
  };

  const { id } = useParams();
  // console.log(id);

  const getdata = async () => {
    const res = await fetch(`http://localhost:4000/individual/${id}`, {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });

    const data = await res.json();
    console.log(data);

    if (res.status === 404 || !data) {
      alert("Error");
      //console.log("error");
    } else {
      setInp(data);
     // console.log("Get data");
    }
  };

  useEffect(() => {
    getdata();
  }, []);

  const updateuser = async (e) => {
    e.preventDefault();

    const { name, email, job, salary } = inpvalue;

    const res2 = await fetch(`http://localhost:4000/updateuser/${id}`, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        name,
        email,
        job,
        salary,
      }),
    });
    const data2 = await res2.json();
   // console.log(data2);

    if (res2.status === 404 || !data2) {
      alert("Error");
      //console.log("error");
    } else {
      // setInp(data2);
      // alert("Your data added");
     // console.log("Get data");
      history("/");
      setUpdata(data2);
    }
  };
  return (
    <div className="container">
      <NavLink to="/">Home</NavLink>
      <form className="mt-4">
        <div className="row">
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputEmail1" class="form-label">
              UserName
            </label>
            <input
              type="text"
              onChange={setData}
              value={inpvalue.name}
              name="name"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
            />
            <div id="emailHelp" class="form-text">
              We'll never share your email with anyone else.
            </div>
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              Email
            </label>
            <input
              type="email"
              name="email"
              value={inpvalue.email}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              job
            </label>
            <input
              type="text"
              name="job"
              value={inpvalue.job}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              salary
            </label>
            <input
              type="number"
              name="salary"
              value={inpvalue.salary}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>

          <button type="submit" onClick={updateuser} class="btn btn-primary">
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default Edit;
