import React, { useContext, useEffect, useState } from "react";
import CreateIcon from "@mui/icons-material/Create";
import DeleteIcon from "@mui/icons-material/Delete";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import WorkIcon from "@mui/icons-material/Work";
import { NavLink, useParams,useNavigate } from "react-router-dom";
// import { deletdata } from "./context/Contextprovide";

const Details = () => {
  const [getuserdata, SetUserdata] = useState([]);
  // const [dltdata, setdltdata] = useContext(deletdata);
  console.log(getuserdata);
  const history = useNavigate("");
  const { id } = useParams();
  console.log(id);

  const getdata = async () => {
    const res = await fetch(`http://localhost:4000/individual/${id}`, {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });

    const data = await res.json();
    //console.log(data);

    if (res.status === 404 || !data) {
      alert("Error");
      //console.log("error");
    } else {
      SetUserdata(data);
     // console.log("Get data");
    }
  };

  useEffect(() => {
    getdata();
  }, []);

  const deleteuser=async(id)=>{

    const res2 = await fetch(`http://localhost:4000/deleteuser/${id}`,{
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
    });

    const deletedata = await res2.json();
    //console.log(deletedata);

    if (res2.status === 404 || !deletedata) {
      alert("Error");
     // console.log("error");
    } else {
      // setInp(deletedata);
      // console.log("user deleted");
      // getdata();
      history("/");
      

    }
  }
  return (
    <div className="container mt-3">
      <h2
        style={{ fontWeight: 400 }}
      >{`Your Profile Mr. ${getuserdata.name}`}</h2>

      <Card sx={{ minWidth: 600 }}></Card>
      <CardContent>
        <div className="add_btn ">
          <NavLink to={`/edit/${getuserdata._id}`}>
            <button className="btn btn-primary">
              <CreateIcon />
            </button>
          </NavLink>
          <button
           onClick={() => deleteuser(getuserdata._id)}
            className="btn btn-danger"
          >
            <DeleteIcon />
          </button>
        </div>
        <div className="left_view">
          <img src="/profile.png" style={{ width: 50 }} alt="pic" />
          <h3 className="mt-3">
            Name: <span style={{ fontWeight: 400 }}>{getuserdata.name}</span>
          </h3>
          <h3 className="mt-3">
            Email: <span style={{ fontWeight: 400 }}>{getuserdata.email}</span>
          </h3>
          <p>
            <WorkIcon /> job : <span>{getuserdata.job}</span>
          </p>
          <p>
            <AttachMoneyIcon /> Salary : <span>{getuserdata.salary}</span>
          </p>
        </div>
      </CardContent>
    </div>
  );
};

export default Details;
