import React, { useContext, useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { adddata } from "./context/Contextprovide";

const Register = () => {

  const history = useNavigate("");
  const {udata,setudata}=useContext(adddata);

  const [inpvalue,setInp]=useState({
    name:"",
    email:"",
    job:"",
    salary:""
  })
    const setData=(e)=>{
      //console.log(e.target.value);
      const {name,value}=e.target;
      setInp((preval)=>{
        return{
          ...preval,
          [name]:value
        }
      })
    }

    const addingdata =async(e)=>{
      e.preventDefault();

      const res= await fetch("http://localhost:4000/register",{
        method:'POST',
        headers:{'Content-Type':'application/json'},
        body:
        JSON.stringify({
            "name": inpvalue.name,
            "email": inpvalue.email,
            "salary":inpvalue.salary,
            "job":inpvalue.job
            }),
      });
      const data=await res.json();
      //console.log(data);

      if(res.status===404 || !data){
        alert("Error");
        //console.log("error");
      }else {
        history("/");
        // alert('Data Added Successfully');
        setudata(data);
        // console.log("added");
      }
    };

  return (
    <div className="container">
      <NavLink to="/">Home</NavLink>
      <form className="mt-4">
        <div className="row">
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputEmail1" class="form-label">
              UserName
            </label>
            <input
              type="text"
              onChange={setData}
              value={inpvalue.name}
              name="name"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
            />
            <div id="emailHelp" class="form-text">
              We'll never share your email with anyone else.
            </div>
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              Email
            </label>
            <input
              type="email"
              name="email"
              value={inpvalue.email}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              job
            </label>
            <input
              type="text"
              name="job"
              value={inpvalue.job}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>
          <div className="mb-3 col-lg-6 col-md-6 col-12">
            <label for="exampleInputPassword1" class="form-label">
              salary
            </label>
            <input
              type="number"
              name="salary"
              value={inpvalue.salary}
              onChange={setData}
              class="form-control"
              id="exampleInputPassword1"
            />
          </div>
         
          
          <button type="submit" onClick={addingdata} class="btn btn-primary">
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default Register;
